.project_details{
padding:2em;
margin-right: auto;
margin-left: auto;
}

.project_details h1,
.project_details h2,
.project_details p {
margin-bottom:0.5em;
}

.project_details h1 {
background:#222;
color:#FFB333;
padding:0.4em;
}

.project_details span{
font-weight:bold;
}

.details_container,.service_form_container{
border-bottom:1px solid #7a7a7a;
margin-bottom:1.2em; padding-bottom:1.2em;
display:flex;
justify-content: space-between;
flex-wrap: wrap;
}

.btn{
    background:#222;
    color:white;
    padding:0.5em 1em;
    text-decoration: none;
    transition:0.5s;
    cursor:pointer;
    max-height:40px;
    border:none;
    }
    
    .btn:hover {
    color:#ffbb33;
    }

    .project_info{
    width:100%;
    }




